  align-items: center;
  height: 100vh;
  display: flex;
}

.grid-header {
  display: none;
}

.tg-grid {
  grid-template-areas: "one"
                       "two"
                       "three"
                       "four"
                       "five";
  grid-auto-columns: 1fr;
  gap: 1.5rem;
  width: min(95%, 73rem);
  min-width: 20rem;
  margin: auto;
  padding: 1.5rem;
  display: grid;
}

.tg-card:first-child {
  grid-area: one;
}

.tg-card:nth-child(2) {
  grid-area: two;
}

.tg-card:nth-child(3) {
  grid-area: three;
}

.tg-card:nth-child(4) {
  grid-area: four;
}

.tg-card:nth-child(5) {
  grid-area: five;
}

@media (width >= 48rem) {
  .tg-grid {
    grid-template-areas: "one one"
                         "two five"
                         "three five"
                         "four five";
    gap: 1.875rem;
  }
}

@media (width >= 62rem) {
  .tg-grid {
    grid-template-areas: "one one two five"
                         "three four four five";
  }
}

